import React , { useEffect,useState } from "react";
import { Row, Col } from "react-bootstrap";
import { Bar, Doughnut } from "react-chartjs-2";
import { Chart as ChartJS } from 'chart.js/auto';

const Sidebar=()=>{
  const [dealsDetails,setDealsDetails] = useState([{}]);//Deatils of deal

  const getDeal= async ()=>{
    
    await fetch('https://ido-api-services.herokuapp.com/get-single-deal/61b753f41a57240016b71a8b?filter={"offset": 0,"order": "string","fields": {"Name": true,"id": true,"Ticker": true,"TotalRaize": true,"Currency": true,"Start": true,"End": true,"Price": true,"DealRate": true,"AllocationForTier1": true,"AllocationForTier2": true,"AllocationForTier3": true,"AllocationForTier4": true,"Icon": true,"CurrentRaize": true}}').then(res=>res.json()).then(
      (result)=>{
        // console.log(result)
          if(result.success){
            setDealsDetails(result.res);
          }
      })
  }
    useEffect(()=>{
      getDeal();
    },[])
    

    return(
        <>
    <div className="side-bar">
    <div className="bar-box">
    <div className="bar-heading">
    <h3>Nakamoto Games{/*dealsDetails.Name*/}</h3>  <img src={"data:image/png;base64,"+dealsDetails.Icon}/>
    </div>
    <div className="bar-cnt">
    <h3> <span style={{fontSize:"12px"}}>${/*dealsDetails.Currency*/} </span>50.01{/*dealsDetails.CurrentRaize*/}</h3>  
    <p>Total Raize</p>
    </div>

      
<div class="line-chart">
<svg width="100%" height="204" viewBox="0 0 340 204" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M0 63.1842C0 59.5218 1.32457 55.984 3.93445 53.4147C11.0146 46.4445 26.4302 33 40 33C65.5 33.0001 77.0923 91.4999 98.5 91.5C119.908 91.5001 131 54 153 54C175 54 183.02 77.0859 214 77.0001C244.98 76.9143 256.02 45.0002 287 45.0001C313.846 45 333.963 85.9232 338.854 96.8468C339.641 98.6045 340 100.49 340 102.416V189C340 197.284 333.284 204 325 204H15C6.71572 204 0 197.284 0 189V63.1842Z" fill="url(#paint0_linear_2_204)"/>
<path opacity="0.1" d="M0 47.1842C0 43.5218 1.32457 39.984 3.93445 37.4147C11.0146 30.4445 26.4302 17 40 17C65.5 17.0001 77.0923 75.4999 98.5 75.5C119.908 75.5001 131 38 153 38C175 38 183.02 61.0859 214 61.0001C244.98 60.9143 256.02 29.0002 287 29.0001C313.846 29 333.963 69.9232 338.854 80.8468C339.641 82.6045 340 84.49 340 86.4158V173C340 181.284 333.284 188 325 188H15C6.71572 188 0 181.284 0 173V47.1842Z" fill="url(#paint1_linear_2_204)"/>
<path opacity="0.1" d="M0 30.1842C0 26.5218 1.32457 22.984 3.93445 20.4147C11.0146 13.4445 26.4302 -3.16047e-05 40 1.84243e-09C65.5 5.9396e-05 77.0923 58.4999 98.5 58.5C119.908 58.5001 131 21 153 21C175 21 183.02 44.0859 214 44.0001C244.98 43.9143 256.02 12.0002 287 12.0001C313.846 12 333.963 52.9232 338.854 63.8468C339.641 65.6045 340 67.49 340 69.4158V156C340 164.284 333.284 171 325 171H15C6.71572 171 0 164.284 0 156V30.1842Z" fill="url(#paint2_linear_2_204)"/>
<defs>
<linearGradient id="paint0_linear_2_204" x1="6.73268" y1="96.5143" x2="335.968" y2="113.964" gradientUnits="userSpaceOnUse">
<stop stop-color="#7C6ADD"/>
<stop offset="1" stop-color="#617EEB"/>
</linearGradient>
<linearGradient id="paint1_linear_2_204" x1="6.73268" y1="80.5143" x2="335.968" y2="97.9642" gradientUnits="userSpaceOnUse">
<stop stop-color="#7C6ADD"/>
<stop offset="1" stop-color="#617EEB"/>
</linearGradient>
<linearGradient id="paint2_linear_2_204" x1="6.73268" y1="63.5143" x2="335.968" y2="80.9642" gradientUnits="userSpaceOnUse">
<stop stop-color="#7C6ADD"/>
<stop offset="1" stop-color="#617EEB"/>
</linearGradient>
</defs>
</svg>
<div class="select-point">
<svg className="slt-crl" width="7" height="7" viewBox="0 0 7 7" fill="none" xmlns="http://www.w3.org/2000/svg">
<circle cx="3.5" cy="3.5" r="3.5" fill="white"/>
</svg>

<svg className="chart-ln" width="1" height="80" viewBox="0 0 1 80" fill="none" xmlns="http://www.w3.org/2000/svg">
<line x1="0.5" y1="80" x2="0.499997" y2="2.18557e-08" stroke="url(#paint0_linear_2_214)"/>
<defs>
<linearGradient id="paint0_linear_2_214" x1="1.00018" y1="-8.9184e-06" x2="1.00009" y2="74.8148" gradientUnits="userSpaceOnUse">
<stop stop-color="white" stop-opacity="0.7"/>
<stop offset="1" stop-color="white" stop-opacity="0"/>
</linearGradient>
</defs>
</svg>
</div>
<div className="number-cnt">
<div className="numb-p"> 5K </div> 
<div className="numb-p"> 10K </div> 
<div className="numb-p"> 25K </div> 
<div className="numb-p"> 50K </div> 
<div className="numb-p"> 75K </div> 
</div>

</div>

    </div> {/* bar box closed */}

    <div className="bar-box">
    <div className="bar-heading">
    <h3 style={{marginBottom:'10px'}}>Contributors statistics</h3>
    </div> 
    
    <Bar
      data={{
        labels:['Top10','Tier1','Tier2','Tier3','Tier4'],
        datasets:[
          {
            label:"Total Amount",
            data:[75,70,50,70,30],
            backgroundColor: '#7C6BDE',
            borderWidth: 1,
            barThickness:5,
            color: "#fff"
          },
          {
            label:"No. of Contributors",
            data:[200,250,300,230,300],
            backgroundColor: '#617EEB',
            borderWidth: 1,
            barThickness:5,
            color: "#fff"
          }
        ]
      }}
      options={{
        plugins: {
          legend: {
            display: false,
            color: '#fff'
          }
        }
      }}
    />
    <div className="bar-cnt d-flex">
      <p style={{marginLeft:'40px'}}></p>
    <p className="tm"><i class="fa fa-circle" aria-hidden="true"></i> Total amount</p>
    <p  className="nc"><i class="fa fa-circle" aria-hidden="true"></i> Nr. of contributors</p>
    </div>
    </div> {/* bar box closed */}

    <div className="bar-box">
    <div className="bar-heading">
      <h3>Contributors statistics</h3> 
    </div> 
    <Row>
      <Col md={4}>
        <div className="progress-div">
        <div className="progress-div-data">
            <p>500/<br/>8000</p>
        </div>
        <Doughnut
          data={{
            datasets: [{
              label: 'My Allocation',
              data: [8000, 500],
              backgroundColor: [
                '#617EEB',
                'rgba(46, 53, 96, 1)'
              ],
              borderWidth: 0,
              hoverOffset: 4
            }]
          }}
          options={{
            cutout:'80%',
            events: []
          }}
        />
        </div>
      </Col>

      <Col md={4}>
        <div className="progress-div">
        <div className="progress-div-data">
            <p>300/<br/>500</p>
        </div>
        <Doughnut
          data={{
            datasets: [{
              label: 'My Contribution',
              data: [300, 200],
              backgroundColor: [
                '#FFD700',
                'rgba(46, 53, 96, 1)'
              ],
              borderWidth: 0,
              hoverOffset: 4
            }]
          }}
          options={{
            cutout:'80%',
            events: []
          }}
        />
        </div>
      </Col>
      
      <Col md={4}>
        <div className="progress-div">
        <div className="progress-div-data">
            <p>500/<br/>8000</p>
        </div>
        <Doughnut
          data={{
            datasets: [{
              label: 'My Contribution vs Total',
              data: [300, 2700],
              backgroundColor: [
                '#7C6BDE',
                'rgba(46, 53, 96, 1)'
              ],
              borderWidth: 0,
              hoverOffset: 4
            }]
          }}
          options={{
            cutout:'80%',
            events: []
          }}
        /></div>
      </Col>      
    </Row>
    <Row>
      <Col md={4} style={{padding:'10px 0px 0px 0px'}}>
        <p className="progress-heading">My Allocation</p>
      </Col>
      <Col md={4} style={{padding:'10px 0px 0px 0px'}}>
        <p className="progress-heading">My Contribution</p>
      </Col>
      <Col md={4} style={{padding:'10px 0px 0px 0px'}}>
        <p className="progress-heading">My Contribution <br/>vs Total</p>
      </Col>
    </Row>
    
    </div> {/* bar box closed */}
    </div> {/* sidebar closed */} 
    </>
    )
}
  export default Sidebar