import React, { useEffect, useState } from "react";
// Route to other page 
import { useLocation, useHistory } from "react-router-dom";
import logo from '../assects/bulk-logo.png';

const Menubar=()=>{
    const [activeMenu,setActiveMenu]=useState(0);
    const [activeSubmenu,setActiveSubmenu]=useState(5);
    // jump to other page
    const history = useHistory()

    const location = useLocation()

    const setSubMenu= async (index)=>{
        // Handle tab selected
        if(index!==activeSubmenu){
            setActiveSubmenu(index);
        }
        switch(index){
            case 0:history.push('/member-area/top10');
                break;
            case 1:history.push('/member-area/tier1')
                break;
            case 2:history.push('/member-area/tier2')
                break;
            case 3:history.push('/member-area/tier3')
                break;
            case 4:history.push('/member-area/tier4')
                break; 
        }
    }

    useEffect(()=>{
        //Check active
        if(location.pathname.startsWith("/my-deals")){
            setActiveMenu(1)
        } else if(location.pathname.startsWith("/member-area/")){
            document.getElementById("check01").checked=true; 
            setActiveMenu(2);
            if(location.pathname.endsWith("/top10")){
                setActiveSubmenu(0);
            } else if(location.pathname.endsWith("/tier1")){
                setActiveSubmenu(1);
            }   else if(location.pathname.endsWith("/tier2")){
                setActiveSubmenu(2);
            }   else if(location.pathname.endsWith("/tier3")){
                setActiveSubmenu(3);
            }   else if(location.pathname.endsWith("/tier4")){
                setActiveSubmenu(4);
            } 
        } else if(location.pathname.startsWith("/profile")){
            setActiveMenu(3)
        } else if(location.pathname.startsWith("/contribute/")){
            setActiveMenu(5)
        } else {
            setActiveMenu(0)
        } 
    },[])
    
    return(
        <>
        <div className="left-nav">
            <div className="Logo"><img src={logo}/></div>
            <ul id="menu">
                <li className={activeMenu==0?"active":""}>
                    <a className="menuItmes" onClick={e=>history.push('/')}>
                        <i class="fa fa-home" aria-hidden="true"></i> Public Area
                    </a>
                </li>
                <li className={activeMenu==1?"active":""}>
                    <a className="menuItmes" onClick={e=>history.push('/my-deals')}>
                        <i class="fa fa-list" aria-hidden="true"></i> My Deals
                    </a>
                    </li>
                <li className={activeMenu==2?"active":""}>
                    <input id="check01" type="checkbox" name="menu"/>
                        <label for="check01">
                        <i class="fa fa-windows" aria-hidden="true"></i> Members Area</label>
                    <ul class="submenu">
                        <li><a className="menuItmes" className={activeSubmenu==0?"active":""} onClick={() => setSubMenu(0)}>Top 10</a></li>
                        <li><a className="menuItmes" className={activeSubmenu==1?"active":""} onClick={() => setSubMenu(1)}>Tier 1</a></li>
                        <li><a className="menuItmes" className={activeSubmenu==2?"active":""} onClick={() => setSubMenu(2)}>Tier 2</a></li>
                        <li><a className="menuItmes" className={activeSubmenu==3?"active":""} onClick={() => setSubMenu(3)}>Tier 3</a></li>
                        <li><a className="menuItmes" className={activeSubmenu==4?"active":""} onClick={() => setSubMenu(4)}>Tier 4</a></li>
                    </ul>
                </li> 
                <li className={activeMenu==3?"active":""}>
                    <a style={{marginTop:'5px'}} className="menuItmes" onClick={e=>history.push('/profile')}>
                        <i class="fa fa-user" aria-hidden="true"></i> My Profile
                    </a>
                </li>
            </ul>

        </div>
        </>
    )
}
  export default Menubar